	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import wenxianziyuan from '@/views/wenxianziyuan/list'
	import examquestion from '@/views/exam/examquestion/list'
	import shipinziyuan from '@/views/shipinziyuan/list'
	import jiaoshi from '@/views/jiaoshi/list'
	import exampaper from '@/views/exampaper/list'
	import storeup from '@/views/storeup/list'
	import discusskechengxinxi from '@/views/discusskechengxinxi/list'
	import kechengxinxi from '@/views/kechengxinxi/list'
	import users from '@/views/users/list'
	import kechengleixing from '@/views/kechengleixing/list'
	import forum from '@/views/forum/list'
	import jiaocaiziyuan from '@/views/jiaocaiziyuan/list'
	import yinpinziyuan from '@/views/yinpinziyuan/list'
	import yonghu from '@/views/yonghu/list'
	import chat from '@/views/chat/list'
	import config from '@/views/config/list'
	import exampaperlist from '@/views/exam/exampaperlist/list'
	import examination from '@/views/exam/exampaperlist/examination'
	import examrecord from '@/views/exam/examrecord/list'
	import examfailrecord from '@/views/exam/examfailrecord/list'
	import jiaoshiRegister from '@/views/jiaoshi/register'
	import jiaoshiCenter from '@/views/jiaoshi/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/jiaoshiCenter',
			name: '教师个人中心',
			component: jiaoshiCenter
		}
		,{
			path: '/news',
			name: '系统公告',
			component: news
		}
		,{
			path: '/wenxianziyuan',
			name: '文献资源',
			component: wenxianziyuan
		}
		,{
			path: '/examquestion',
			name: '试题管理',
			component: examquestion
		}
		,{
			path: '/shipinziyuan',
			name: '视频资源',
			component: shipinziyuan
		}
		,{
			path: '/jiaoshi',
			name: '教师',
			component: jiaoshi
		}
		,{
			path: '/exampaper',
			name: '试卷管理',
			component: exampaper
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/discusskechengxinxi',
			name: '课程信息评论',
			component: discusskechengxinxi
		}
		,{
			path: '/kechengxinxi',
			name: '课程信息',
			component: kechengxinxi
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/kechengleixing',
			name: '课程类型',
			component: kechengleixing
		}
		,{
			path: '/forum',
			name: '学习社区',
			component: forum
		}
		,{
			path: '/jiaocaiziyuan',
			name: '教材资源',
			component: jiaocaiziyuan
		}
		,{
			path: '/yinpinziyuan',
			name: '音频资源',
			component: yinpinziyuan
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/chat',
			name: '在线咨询',
			component: chat
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		, {
			path: '/exampaperlist',
			name: '考试列表',
			component: exampaperlist
		}, {
			path: '/examrecord',
			name: '考试记录',
			component: examrecord
		}, {
			path: '/examfailrecord',
			name: '错题本',
			component: examfailrecord
		}
		]
	},
	{
		path: '/jiaoshiRegister',
		name: '教师注册',
		component: jiaoshiRegister
	},
	{
		path: '/examination',
		name: '考试',
		component: examination
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
