<template>
	<div>
		<div class="register_view">
			<el-form :model="registerForm" class="register_form">
				<div class="title_view">{{projectName}}注册</div>
				<div class="list_item">
					<div class="list_label">教师账号：</div>
					<el-input class="list_inp"
						 v-model="registerForm.jiaoshizhanghao" 
						 placeholder="请输入教师账号"
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label">教师密码：</div>
					<el-input class="list_inp"
						 v-model="registerForm.jiaoshimima" 
						 placeholder="请输入教师密码"
						 type="password"
						 />
				</div>
				<div class="list_item">
					<div class="list_label">确认教师密码：</div>
					<el-input class="list_inp" v-model="registerForm.jiaoshimima2" type="password" placeholder="请输入确认教师密码" />
				</div>
				<div class="list_item">
					<div class="list_label">教师姓名：</div>
					<el-input class="list_inp"
						 v-model="registerForm.jiaoshixingming" 
						 placeholder="请输入教师姓名"
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label">性别：</div>
					<el-select 
						class="list_sel"
						v-model="registerForm.xingbie" 
						placeholder="请选择性别"
						>
						<el-option v-for="item in jiaoshixingbieLists" :label="item" :value="item"></el-option>
					</el-select>
				</div>

				<div class="list_item">
					<div class="list_label">图片：</div>
					<div :style='{"width":"calc(100% - 120px)"}' class="list_file_list">
						<uploads
							action="file/upload" 
							tip="请上传图片" 
							:limit="3"
							:fileUrls="registerForm.tupian?registerForm.tupian:''" 
							@change="tupianUploadSuccess">
						</uploads>
					</div>
				</div>
				<div class="list_item">
					<div class="list_label">联系方式：</div>
					<el-input class="list_inp"
						 v-model="registerForm.lianxifangshi" 
						 placeholder="请输入联系方式"
						 type="text"
						/>
				</div>
				<div class="list_btn">
					<el-button class="register" type="success" @click="handleRegister">注册</el-button>
					<div class="r-login" @click="close">已有账号，直接登录</div>
				</div>
			</el-form>
		</div>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context?.$project.projectName
	//获取注册类型
	import { useRoute } from 'vue-router';
	const route = useRoute()
	const tableName = ref('jiaoshi')
	
	//公共方法
	const getUUID=()=> {
		return new Date().getTime();
	}
	const registerForm = ref({
        xingbie: '',
	})
	const jiaoshixingbieLists = ref([])
	const init=()=>{
		jiaoshixingbieLists.value = "男,女".split(',')
	}
    const tupianUploadSuccess=(fileUrls)=> {
        registerForm.value.tupian = fileUrls;
    }
	// 多级联动参数
	//注册按钮
	const handleRegister = () => {
		let url = tableName.value +"/register";
		if((!registerForm.value.jiaoshizhanghao)){
			context?.$toolUtil.message(`教师账号不能为空`,'error')
			return false
		}
		if((!registerForm.value.jiaoshimima)){
			context?.$toolUtil.message(`教师密码不能为空`,'error')
			return false
		}
		if(registerForm.value.jiaoshimima!=registerForm.value.jiaoshimima2){
			context?.$toolUtil.message('两次密码输入不一致','error')
			return false
		}
		if((!registerForm.value.jiaoshixingming)){
			context?.$toolUtil.message(`教师姓名不能为空`,'error')
			return false
		}
		if(registerForm.value.tupian!=null){
			registerForm.value.tupian = registerForm.value.tupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(registerForm.value.lianxifangshi&&(!context?.$toolUtil.isMobile(registerForm.value.lianxifangshi))){
			context?.$toolUtil.message(`联系方式应输入手机格式`,'error')
			return false
		}
		
		context?.$http({
			url:url,
			method:'post',
			data:registerForm.value
		}).then(res=>{
			context?.$toolUtil.message('注册成功','success', obj=>{
				context?.$router.push({
					path: "/login"
				});
			})
		})
	}
	//返回登录
	const close = () => {
		context?.$router.push({
			path: "/login"
		});
	}
	init()
</script>
<style lang="scss" scoped>
	
	.register_view {
		background-repeat: no-repeat;
		flex-direction: column;
		background-size: cover;
		background: url(http://clfile.zggen.cn/20231005/6f942d87e2e645d0bc52b1cf4454beb8.png);
		display: flex;
		min-height: 100vh;
		justify-content: center;
		align-items: center;
		position: relative;
		background-position: center center;
		// 表单盒子
		.register_form {
			border-radius: 0;
			padding: 30px 0 60px;
			box-shadow: 4px 0 6px rgba(0,0,0,0.5);
			background: #fff;
			display: flex;
			width: 800px;
			flex-wrap: wrap;
		}
		// 标题样式
		.title_view {
			padding: 30px 0 30px 40px;
			margin: 0 0 20px -20px;
			color: #fff;
			background: rgba(75,137,243,1);
			width: 94%;
			font-size: 20px;
			text-align: left;
		}
		// item盒子
		.list_item {
			margin: 10px auto;
			display: flex;
			width: 83%;
			justify-content: flex-start;
			align-items: center;
			// label
			.list_label {
				width: 90px;
				font-size: 14px;
				box-sizing: border-box;
				text-align: center;
			}
			// 输入框
			:deep(.list_inp) {
				border: 1px solid #ddd;
				padding: 0 10px;
				width: calc(100% - 90px);
				line-height: 36px;
				box-sizing: border-box;
				height: 36px;
				//去掉默认样式
				.el-input__wrapper{
					border: none;
					box-shadow: none;
					background: none;
					border-radius: 0;
					height: 100%;
					padding: 0;
				}
				.is-focus {
					box-shadow: none !important;
				}
			}
		}
		//下拉框样式
		:deep(.list_sel) {
			border: 1px solid #ddd;
			border-radius: 0;
			padding: 0 10px;
			width: calc(100% - 90px);
			line-height: 36px;
			box-sizing: border-box;
			height: 36px;
			//去掉默认样式
			.select-trigger{
				height: 100%;
				.el-input{
					height: 100%;
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
			}
		}
		//按钮盒子
		.list_btn {
			margin: 20px auto 0;
			display: flex;
			width: 100%;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			//注册按钮
			.register {
					border: none;
					border-radius: 0;
					color: #fff;
					background: rgba(75,137,243,1);
					width: 83%;
					font-size: 20px;
					height: 50px;
			}
			//注册按钮悬浮样式
			.register:hover {
				border: none;
				border-radius: 0;
				background: rgba(75,137,243,0.5);
				width: 83%;
				font-size: 20px;
				height: 50px;
			}
			//已有账号
			.r-login {
				cursor: pointer;
				padding: 20px 0 0;
				color: #999;
				width: 100%;
				font-size: 12px;
				text-align: center;
			}
		}
		//图片上传样式
		.list_file_list  {
			//提示语
			:deep(.el-upload__tip){
				margin: 7px 0 0;
				color: #999;
				display: flex;
				font-size: 12px;
				justify-content: flex-start;
				align-items: center;
			}
			//外部盒子
			:deep(.el-upload--picture-card){
				border: 1px dashed #000;
				cursor: pointer;
				background-color: #fff;
				border-radius: 8px;
				width: 100px;
				line-height: 110px;
				text-align: center;
				height: 100px;
				//图标
				.el-icon{
					color: #000;
					font-size: 32px;
				}
			}
			:deep(.el-upload-list__item) {
				border: 1px dashed #000;
				cursor: pointer;
				background-color: #fff;
				border-radius: 8px;
				width: 100px;
				line-height: 110px;
				text-align: center;
				height: 100px;
			}
		}
	}
</style>